import React from 'react';
import { Table } from 'react-bootstrap';


const AllList = ({info}) => {

  

    return (
        <div>
            
            <Table striped bordered hover>
            <thead>
    <tr>
      <th>student name</th>
      <th>email</th>
      <th>phone no</th>
      <th>birth date</th>
      <th>Subjects</th>
     

    </tr>
  </thead>
  <tbody>
  <tr>
      <td>{info.name}</td>
      <td>{info.email}</td>
      
      <td>{info.phone}</td>
      <td>{info.birth}</td>
      <td>{info.sub1},{info.sub2}</td>
     
    </tr>
  </tbody>

        </Table>


           
        </div>
    );
};

export default AllList;